/* Modern Design System - Calcio Mapper */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

/* Global reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}



html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

/* CSS Variables for consistent design */
:root {
  /* Colors */
  --primary-50: #eff6ff;
  --primary-100: #dbeafe;
  --primary-200: #bfdbfe;
  --primary-300: #93c5fd;
  --primary-400: #60a5fa;
  --primary-500: #3b82f6;
  --primary-600: #2563eb;
  --primary-700: #1d4ed8;
  --primary-800: #1e40af;
  --primary-900: #1e3a8a;
  
  --secondary-50: #fdf4ff;
  --secondary-100: #fae8ff;
  --secondary-200: #f5d0fe;
  --secondary-300: #f0abfc;
  --secondary-400: #e879f9;
  --secondary-500: #d946ef;
  --secondary-600: #c026d3;
  --secondary-700: #a21caf;
  --secondary-800: #86198f;
  --secondary-900: #701a75;
  
  --success-50: #f0fdf4;
  --success-100: #dcfce7;
  --success-200: #bbf7d0;
  --success-300: #86efac;
  --success-400: #4ade80;
  --success-500: #22c55e;
  --success-600: #16a34a;
  --success-700: #15803d;
  --success-800: #166534;
  --success-900: #14532d;
  
  --warning-50: #fffbeb;
  --warning-100: #fef3c7;
  --warning-200: #fde68a;
  --warning-300: #fcd34d;
  --warning-400: #fbbf24;
  --warning-500: #f59e0b;
  --warning-600: #d97706;
  --warning-700: #b45309;
  --warning-800: #92400e;
  --warning-900: #78350f;
  
  --error-50: #fef2f2;
  --error-100: #fee2e2;
  --error-200: #fecaca;
  --error-300: #fca5a5;
  --error-400: #f87171;
  --error-500: #ef4444;
  --error-600: #dc2626;
  --error-700: #b91c1c;
  --error-800: #991b1b;
  --error-900: #7f1d1d;
  
  --gray-50: #f9fafb;
  --gray-100: #f3f4f6;
  --gray-200: #e5e7eb;
  --gray-300: #d1d5db;
  --gray-400: #9ca3af;
  --gray-500: #6b7280;
  --gray-600: #4b5563;
  --gray-700: #374151;
  --gray-800: #1f2937;
  --gray-900: #111827;
  
  /* Typography */
  --font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
  --font-size-xs: 0.75rem;
  --font-size-sm: 0.875rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.125rem;
  --font-size-xl: 1.25rem;
  --font-size-2xl: 1.5rem;
  --font-size-3xl: 1.875rem;
  --font-size-4xl: 2.25rem;
  
  /* Spacing */
  --spacing-1: 0.25rem;
  --spacing-2: 0.5rem;
  --spacing-3: 0.75rem;
  --spacing-4: 1rem;
  --spacing-5: 1.25rem;
  --spacing-6: 1.5rem;
  --spacing-8: 2rem;
  --spacing-10: 2.5rem;
  --spacing-12: 3rem;
  --spacing-16: 4rem;
  
  /* Border Radius */
  --radius-sm: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-2xl: 1.5rem;
  --radius-full: 9999px;
  
  /* Shadows */
  --shadow-sm: 0 1px 2px 0 rgb(0 0 0 / 0.05);
  --shadow-md: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  --shadow-lg: 0 10px 15px -3px rgb(0 0 0 / 0.1), 0 4px 6px -4px rgb(0 0 0 / 0.1);
  --shadow-xl: 0 20px 25px -5px rgb(0 0 0 / 0.1), 0 8px 10px -6px rgb(0 0 0 / 0.1);
  --shadow-2xl: 0 25px 50px -12px rgb(0 0 0 / 0.25);
  
  /* Transitions */
  --transition-fast: 150ms ease;
  --transition-normal: 250ms ease;
  --transition-slow: 350ms ease;
}

/* Reset and base styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*::before,
*::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: var(--font-family);
  font-size: var(--font-size-base);
  line-height: 1.6;
  color: var(--gray-900);
  background: linear-gradient(135deg, var(--gray-50) 0%, var(--gray-100) 100%);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
}

/* App container */
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Allow scrolling for specific pages */
.App.scrollable {
  overflow-y: auto;
}

.main-content {
  flex: 1;
}

/* Loading states */
.loading, .map-loading {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  gap: var(--spacing-6);
  background: linear-gradient(135deg, var(--primary-50) 0%, var(--secondary-50) 100%);
}

.spinner {
  width: 3rem;
  height: 3rem;
  border: 3px solid var(--gray-200);
  border-top: 3px solid var(--primary-500);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

.spinner-small {
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-top: 2px solid white;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Modern Navbar */
.navbar {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-bottom: 1px solid var(--gray-200);
  padding: var(--spacing-3) 0;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: var(--shadow-md);
}

.navbar-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--spacing-8);
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--spacing-8);
}

.navbar-brand {
  display: flex;
  align-items: center;
  gap: var(--spacing-4);
  text-decoration: none;
  color: inherit;
}

.brand-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--secondary-500) 100%);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: var(--shadow-md);
}

.brand-text {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-1);
}

.brand-title {
  font-size: var(--font-size-lg);
  font-weight: 700;
  color: var(--gray-900);
  line-height: 1;
}

.brand-subtitle {
  font-size: var(--font-size-xs);
  color: var(--gray-500);
  font-weight: 500;
  line-height: 1;
}



.navbar-left {
  display: flex;
  gap: var(--spacing-4);
  align-items: center;
}

.navbar-right {
  display: flex;
  gap: var(--spacing-4);
  align-items: center;
}

.navbar-actions {
  display: flex;
  gap: var(--spacing-3);
  align-items: center;
}



.navbar-button {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  padding: var(--spacing-2) var(--spacing-4);
  border-radius: var(--radius-lg);
  text-decoration: none;
  color: var(--gray-700);
  transition: all var(--transition-normal);
  border: none;
  background: transparent;
  cursor: pointer;
  font-size: var(--font-size-sm);
  font-weight: 600;
  position: relative;
  overflow: hidden;
}

.navbar-button.primary {
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--primary-600) 100%);
  color: white;
  box-shadow: var(--shadow-sm);
}

.navbar-button.primary:hover {
  background: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-700) 100%);
  transform: translateY(-2px);
  box-shadow: var(--shadow-md);
}

.navbar-button.secondary {
  background: var(--gray-100);
  color: var(--gray-700);
  border: 1px solid var(--gray-200);
}

.navbar-button.secondary:hover {
  background: var(--gray-200);
  transform: translateY(-1px);
  box-shadow: var(--shadow-sm);
}

.navbar-button.logout {
  background: linear-gradient(135deg, var(--error-500) 0%, var(--error-600) 100%);
  color: white;
}

.navbar-button.logout::before {
  display: none;
}

.navbar-button.logout:hover {
  background: linear-gradient(135deg, var(--error-600) 0%, var(--error-700) 100%);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

/* User Profile in Navbar */
.user-profile {
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
  padding: var(--spacing-2) var(--spacing-4);
  background: var(--gray-50);
  border-radius: var(--radius-xl);
  border: 1px solid var(--gray-200);
  transition: all var(--transition-normal);
}

.user-profile:hover {
  background: var(--gray-100);
  transform: translateY(-1px);
  box-shadow: var(--shadow-md);
}

.user-avatar {
  position: relative;
  width: 2.5rem;
  height: 2.5rem;
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-weight: 700;
  font-size: var(--font-size-sm);
  box-shadow: var(--shadow-md);
  animation: avatarPulse 2s ease-in-out infinite;
}

.avatar-text {
  z-index: 2;
  position: relative;
}

.avatar-ring {
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  border: 2px solid transparent;
  border-radius: var(--radius-full);
  background: linear-gradient(45deg, var(--primary-500), var(--secondary-500)) border-box;
  -webkit-mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask: linear-gradient(#fff 0 0) padding-box, linear-gradient(#fff 0 0);
  mask-composite: exclude;
  animation: avatarRotate 3s linear infinite;
}

@keyframes avatarPulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

@keyframes avatarRotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.user-info {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-1);
}

.user-name {
  font-weight: 600;
  color: var(--gray-900);
  font-size: var(--font-size-sm);
  line-height: 1;
}

.user-status {
  font-size: var(--font-size-xs);
  color: var(--success-600);
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: var(--spacing-1);
}

.user-status::before {
  content: '';
  width: 6px;
  height: 6px;
  background: var(--success-500);
  border-radius: var(--radius-full);
  animation: statusPulse 2s ease-in-out infinite;
}

@keyframes statusPulse {
  0%, 100% {
    opacity: 1;
    transform: scale(1);
  }
  50% {
    opacity: 0.7;
    transform: scale(1.2);
  }
}

/* Map container */
.map-container {
  height: 100vh;
  height: 100dvh; /* Dynamic viewport height for mobile */
  position: relative;
  background: transparent;
  margin: 0;
  padding: 0;
  /* Allow map to extend behind status bar and Dynamic Island */
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}



/* Map styles */
.map {
  flex: 1;
  border-radius: 0;
  overflow: hidden;
  box-shadow: none;
  margin: 0;
}

.leaflet-container {
  background: linear-gradient(135deg, var(--gray-50) 0%, var(--gray-100) 100%);
  font-family: var(--font-family);
}

.leaflet-control-zoom {
  border-radius: var(--radius-xl) !important;
  box-shadow: var(--shadow-lg) !important;
  border: none !important;
  overflow: hidden;
  /* Position controls to avoid status bar */
  top: 60px !important;
  right: 20px !important;
}

.leaflet-control-zoom a {
  background: rgba(255, 255, 255, 0.95) !important;
  backdrop-filter: blur(10px);
  border: none !important;
  color: var(--gray-700) !important;
  font-weight: 600 !important;
  transition: all var(--transition-normal) !important;
  font-size: var(--font-size-lg) !important;
}

.leaflet-control-zoom a:hover {
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--primary-600) 100%) !important;
  color: white !important;
  transform: scale(1.05);
}

/* Modern marker styles */
.modern-marker {
  filter: drop-shadow(0 4px 12px rgba(0,0,0,0.15));
  transition: all var(--transition-normal);
}

.modern-marker:hover {
  filter: drop-shadow(0 8px 24px rgba(0,0,0,0.25));
  transform: scale(1.15);
}

/* Marker popup styles */
.marker-popup {
  text-align: center;
  min-width: 280px;
  padding: var(--spacing-2);
}

.marker-popup h3 {
  margin: 0 0 var(--spacing-3) 0;
  color: var(--gray-900);
  font-size: var(--font-size-lg);
  font-weight: 700;
  background: linear-gradient(135deg, var(--primary-600) 0%, var(--secondary-600) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.marker-popup p {
  margin: var(--spacing-2) 0;
  color: var(--gray-600);
  font-size: var(--font-size-sm);
  line-height: 1.5;
}

.popup-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-2);
  width: 100%;
  margin-top: var(--spacing-4);
  padding: var(--spacing-3) var(--spacing-4);
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--primary-600) 100%);
  color: white;
  border: none;
  border-radius: var(--radius-lg);
  cursor: pointer;
  font-size: var(--font-size-sm);
  font-weight: 600;
  transition: all var(--transition-normal);
  box-shadow: var(--shadow-md);
}

.popup-button:hover {
  transform: translateY(-2px);
  box-shadow: var(--shadow-lg);
  background: linear-gradient(135deg, var(--primary-600) 0%, var(--primary-700) 100%);
}

/* Modal styles */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 10000;
  padding: var(--spacing-4);
}

/* Special modal for location selection - minimal overlay */
.modal-overlay.location-selection {
  align-items: flex-start;
  justify-content: flex-end;
  padding: var(--spacing-4);
  padding-top: 80px;
  padding-right: var(--spacing-4);
  background: transparent;
  backdrop-filter: none;
  pointer-events: none;
  z-index: 10001 !important;
}

.modal-overlay.location-selection .modal-content {
  max-width: 320px;
  width: 100%;
  max-height: auto;
  margin-left: auto;
  margin-right: 0;
  background: rgba(255, 255, 255, 0.98);
  backdrop-filter: blur(10px);
  border: 2px solid var(--primary-500);
  box-shadow: 0 8px 32px rgba(59, 130, 246, 0.3);
  pointer-events: auto;
  animation: pulse 2s infinite;
}

@keyframes pulse {
  0% {
    box-shadow: 0 8px 32px rgba(59, 130, 246, 0.3);
  }
  50% {
    box-shadow: 0 8px 32px rgba(59, 130, 246, 0.6);
  }
  100% {
    box-shadow: 0 8px 32px rgba(59, 130, 246, 0.3);
  }
}

/* Ensure modal overlay is hidden when not needed */
.modal-overlay:not(:has(.modal-content)) {
  display: none;
}

/* Force hide modal overlay when modal is closed */
.modal-overlay.hidden {
  display: none !important;
  opacity: 0 !important;
  visibility: hidden !important;
  pointer-events: none !important;
}

/* Hide modal overlay when it doesn't contain modal content */
.modal-overlay:not(:has(.modal-content)) {
  display: none !important;
}

.modal-content {
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(20px);
  border-radius: var(--radius-2xl);
  box-shadow: var(--shadow-2xl);
  max-width: 480px;
  width: 100%;
  max-height: 90vh;
  overflow: hidden;
  animation: modalSlideIn var(--transition-slow) ease;
  border: 1px solid var(--gray-200);
  display: flex;
  flex-direction: column;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-40px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-4) var(--spacing-6);
  border-bottom: 1px solid var(--gray-200);
  background: white;
  border-radius: var(--radius-2xl) var(--radius-2xl) 0 0;
  flex-shrink: 0;
}

.modal-title {
  display: flex;
  align-items: center;
  gap: var(--spacing-4);
}

.field-icon {
  width: 3rem;
  height: 3rem;
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--secondary-500) 100%);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: var(--shadow-md);
}

.modal-header h2 {
  margin: 0;
  color: var(--gray-900);
  font-size: var(--font-size-xl);
  font-weight: 700;
}

.field-subtitle {
  margin: var(--spacing-1) 0 0 0;
  color: var(--gray-600);
  font-size: var(--font-size-sm);
  font-weight: 500;
}

.close-button {
  background: var(--gray-100);
  border: 1px solid var(--gray-200);
  color: var(--gray-600);
  cursor: pointer;
  padding: var(--spacing-2);
  border-radius: var(--radius-lg);
  transition: all var(--transition-normal);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 2.5rem;
  height: 2.5rem;
}

.close-button:hover {
  background: var(--error-50);
  color: var(--error-600);
  border-color: var(--error-200);
  transform: scale(1.05);
}

.modal-body {
  padding: var(--spacing-6);
  overflow-y: auto;
  flex: 1;
  max-height: calc(85vh - 120px);
}



.info-row:last-child {
  border-bottom: none;
}

.info-label {
  font-weight: 500;
  color: var(--gray-600);
}

.info-value {
  font-weight: 600;
  color: var(--gray-900);
}



/* Field Modal styles */
.modal-content.field-modal {
  max-width: 600px !important;
  width: 90vw !important;
  max-height: 85vh;
  overflow-y: auto;
}

.field-details {
  padding: var(--spacing-4);
}

.field-info {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
}

.info-section {
  background: white;
  border: 1px solid var(--gray-200);
  border-radius: var(--radius-lg);
  padding: var(--spacing-4);
  box-shadow: var(--shadow-sm);
}

.section-header {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  margin-bottom: var(--spacing-3);
  padding-bottom: var(--spacing-2);
  border-bottom: 1px solid var(--gray-200);
}

.section-header h3 {
  margin: 0;
  color: var(--gray-900);
  font-size: var(--font-size-lg);
  font-weight: 600;
}

.info-content {
  display: flex !important;
  flex-direction: column !important;
  gap: var(--spacing-2) !important;
}

.info-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: var(--spacing-2) 0;
  border-bottom: 1px solid var(--gray-100);
  width: 100% !important;
}

.info-row:last-child {
  border-bottom: none;
}

.info-label {
  font-weight: 500;
  color: var(--gray-600);
  font-size: var(--font-size-sm);
}

.info-value {
  font-weight: 600;
  color: var(--gray-900);
  text-align: right;
  font-size: var(--font-size-sm);
}

.description-text {
  color: var(--gray-700);
  line-height: 1.6;
  font-size: var(--font-size-sm);
  padding: var(--spacing-2) 0;
}

.features-list {
  display: flex !important;
  flex-wrap: wrap !important;
  gap: var(--spacing-3) !important;
}

.feature-item {
  display: flex;
  align-items: center;
  gap: var(--spacing-2);
  padding: var(--spacing-2) var(--spacing-3);
  background: var(--gray-50);
  border-radius: var(--radius-md);
  color: var(--gray-700);
  font-size: var(--font-size-sm);
  flex: 1 1 calc(50% - var(--spacing-3)) !important;
  min-width: 0 !important;
}



.feature-item svg {
  color: var(--primary-500);
  flex-shrink: 0;
}

.field-actions {
  display: flex;
  gap: var(--spacing-3);
  justify-content: flex-end;
  padding-top: var(--spacing-4);
  border-top: 1px solid var(--gray-200);
  margin-top: var(--spacing-4);
}

.field-icon {
  width: 2.5rem;
  height: 2.5rem;
  background: linear-gradient(135deg, var(--primary-500) 0%, var(--secondary-500) 100%);
  border-radius: var(--radius-full);
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  box-shadow: var(--shadow-md);
}

.field-subtitle {
