/* Modal Header, Body, Footer - following existing design system */
.modal-header {
  padding: var(--spacing-6) var(--spacing-6) var(--spacing-5) var(--spacing-6);
  border-bottom: 1px solid var(--gray-200);
  background: var(--gray-50);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.modal-header h2 {
  font-size: var(--font-size-xl);
  font-weight: 600;
  color: var(--gray-900);
  margin: 0;
  display: flex;
  align-items: center;
  gap: var(--spacing-3);
}

.modal-close {
  background: var(--gray-100);
  border: none;
  border-radius: var(--radius-lg);
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all var(--transition-fast);
  color: var(--gray-600);
}

.modal-close:hover {
  background: var(--gray-200);
  color: var(--gray-800);
  transform: scale(1.05);
}

.modal-body {
  padding: var(--spacing-6);
  flex: 1;
}

.modal-footer {
  padding: var(--spacing-5) var(--spacing-6) var(--spacing-6) var(--spacing-6);
  border-top: 1px solid var(--gray-200);
  background: var(--gray-50);
  display: flex;
  justify-content: center;
}

/* Sport Info Section */
.sport-info {
  display: flex;
  align-items: center;
  gap: var(--spacing-5);
  padding: var(--spacing-5);
  background: var(--primary-50);
  border-radius: var(--radius-xl);
  border: 1px solid var(--primary-200);
  margin-bottom: var(--spacing-6);
}

.sport-icon {
  width: 64px;
  height: 64px;
  background: var(--primary-500);
  border-radius: var(--radius-xl);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-md);
}

.sport-emoji {
  font-size: 32px;
}

.sport-details h3 {
  font-size: var(--font-size-lg);
  font-weight: 600;
  color: var(--gray-900);
  margin: 0 0 var(--spacing-2) 0;
}

.sport-details p {
  color: var(--gray-600);
  font-size: var(--font-size-sm);
  margin: 0;
  line-height: 1.5;
}

/* Action Buttons */
.action-buttons {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-4);
}

.btn {
  padding: var(--spacing-4) var(--spacing-5);
  border: none;
  border-radius: var(--radius-lg);
  font-size: var(--font-size-base);
  font-weight: 500;
  cursor: pointer;
  transition: all var(--transition-fast);
  display: flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-3);
  text-decoration: none;
}

.btn-primary {
  background: var(--primary-500);
  color: white;
  box-shadow: var(--shadow-md);
}

.btn-primary:hover {
  background: var(--primary-600);
  transform: translateY(-1px);
  box-shadow: var(--shadow-lg);
}

.btn-secondary {
  background: var(--gray-100);
  color: var(--gray-700);
  border: 1px solid var(--gray-300);
}

.btn-secondary:hover {
  background: var(--gray-200);
  border-color: var(--gray-400);
}

.btn-outline {
  background: transparent;
  color: var(--gray-600);
  border: 2px solid var(--gray-300);
}

.btn-outline:hover {
  background: var(--gray-50);
  border-color: var(--gray-400);
  color: var(--gray-700);
}

.full-width {
  width: 100%;
}


